.header-container {
    background-color: #212529!important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    font-family: 'Croissant One', cursive;
    font-size: 17px;
  }
  
  .logo {
    /* Assuming you want to style the link */
    text-decoration: none;
    color: white;
    display: flex;
    align-items: center;
  }
  
  .logo img {
    width: 50px; /* Adjust the width of the logo image */
    height: auto; /* Maintain the aspect ratio */
    margin-right: 10px; /* Add some space between the logo and text */
  }
  
  .other-content {
    display: flex;
  }
  
  .other-content a {
    color: white;
    margin-left: 20px;
    text-decoration: none;
  }
  @keyframes floatAnimation {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
    100% {
      transform: translateY(0);
    }
  }
  
  .floating-logo {
    width: 50px;
    height: auto;
    margin-right: 10px;
    animation: floatAnimation 3s ease infinite; /* Adjust the duration and timing function as needed */
  }
 
